<template>
  <div>
    <div class="banner" :style="cover">
      <h1 class="banner-title"></h1>
    </div>
    <v-card class="blog-container">
      <div>
        <span class="info-title"></span>
      </div>
      <v-row class="info-wrapper">
        <v-col md="3" cols="12">
          <!--          <button id="pick-avatar">
            <v-avatar size="140">
              <img :src="this.$store.state.avatar" />
            </v-avatar>
          </button>
          <avatar-cropper
            @uploaded="uploadAvatar"
            trigger="#pick-avatar"
            upload-url="http://localhost:8800/shiyi/user/updateAvatar"
          />-->
          <el-upload class="avatar-uploader" :show-file-list="false" ref="upload" name="filedatas" :action="uploadPictureHost" :before-upload="uploadBefore" :http-request="uploadSectionFile" multiple>
            <img style="border-radius: 50%;width: 120px;" v-if="this.$store.state.avatar" :src="this.$store.state.avatar" class="imgAvatar">
            <i v-else class="el-icon-plus avatar-img-icon"></i>
          </el-upload>
        </v-col>
        <v-col md="7" cols="12">
          <v-text-field v-model="userInfo.nickname" label="" placeholder="" />
          <v-text-field v-model="userInfo.webSite" class="mt-7" label="" placeholder="http://" />
          <v-text-field v-model="userInfo.intro" class="mt-7" label="" placeholder="" />
          <div v-if="loginType !== 0" class="mt-7 binding">
            <v-text-field disabled v-model="email" label="" placeholder="" />
            <v-btn v-if="email" text small @click="openEmailModel">
              
            </v-btn>
            <v-btn v-else text small @click="openEmailModel">
              
            </v-btn>
          </div>
          <v-btn @click="updateUserInfo" outlined class="mt-5"></v-btn>
        </v-col>
      </v-row>
    </v-card>
  </div>
</template>

<script>
import { updateUser, upload } from "../../api";
import AvatarCropper from "vue-avatar-cropper";
export default {
  metaInfo: {
    meta: [
      {
        name: "keyWords",
        content: ",,www.chales.online", //
      },
      {
        name: "description",
        content:
          ",,,",
      },
    ],
  },
  components: { AvatarCropper },
  data: function () {
    return {
      uploadPictureHost: process.env.VUE_APP_BASE_API + "/file/upload",
      // 
      loading: [],
      files: {},
      userInfo: {
        nickname: this.$store.state.nickname,
        intro: this.$store.state.intro,
        avatar: this.$store.state.avatar,
        webSite: this.$store.state.webSite,
      },
    };
  },
  methods: {
    updateUserInfo() {
      (this.userInfo.avatar = this.$store.state.avatar),
        updateUser(this.userInfo)
          .then((res) => {
            this.$store.commit("updateUserInfo", this.userInfo);
            this.$toast({ type: "success", message: "" });
          })
          .catch((err) => {
            this.$toast({ type: "error", message: err.message });
          });
    },
    uploadBefore: function () {
      this.openLoading();
    },
    uploadSectionFile: function (param) {
      this.files = param.file;
      // FormData 
      var formData = new FormData();
      // 
      formData.append("multipartFile", this.files);
      upload(formData)
        .then((res) => {
          this.$store.commit("updateAvatar", res.data);
          this.$toast({ type: "success", message: "" });
        })
        .catch((err) => {
          this.$toast({ type: "error", message: err.message });
        });
      this.loading.close();
    },
    // 
    openLoading: function () {
      this.loading = this.$loading({
        lock: true,
        text: "~",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
    },
    openEmailModel() {
      this.$store.state.emailFlag = true;
    },
  },
  computed: {
    email() {
      return this.$store.state.email;
    },
    loginType() {
      return this.$store.state.loginType;
    },
    cover() {
      var cover = "";
      this.$store.state.blogInfo.pageList.forEach((item) => {
        if (item.pageLabel == "user") {
          cover = item.pageCover;
        }
      });
      return "background: url(" + cover + ") center center / cover no-repeat";
    },
  },
};
</script>

<style scoped>
.info-title {
  font-size: 1.25rem;
  font-weight: bold;
}
.info-wrapper {
  margin-top: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
#pick-avatar {
  outline: none;
}
.binding {
  display: flex;
  align-items: center;
}
</style>
